import React from 'react';

//MUI
import { makeStyles } from '@material-ui/core/styles';
import { Grid, Paper, Slide, Fade, Zoom, Grow, Typography } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  root: props=> ({
      padding:props.imageRadius ? theme.spacing(2):theme.spacing(0),
      alignContent:'center',
      background: props.backgroundGradient ? (`linear-gradient(${props.backgroundGradient[2] ? (props.backgroundGradient[2]): 45}deg, ${props.backgroundGradient[0]} 30%, ${props.backgroundGradient[1]} 90%)`):
                  (props.backgroundColor)
  }),
  image:props=>({
      width:'100%',
      borderRadius:props.imageRadius ? props.imageRadius : '0%'
  }),
}));

export const SlideTestimonial = (props) => {
  const classes = useStyles(props);
    return (
        
            props.transition ? (
                <Slide direction={props.direction ? props.direction : "left"} in={true} timeout={props.speed ? props.speed : 1000} mountOnEnter unmountOnExit>
                    <Paper className={classes.root} elevation={props.shadow ? props.shadow : 1}>
                        <Grid container spacing={1}>
                            {
                                props.image ? (
                                    <Grid item xs={12} sm={12} xl={12} lg={12}>
                                        <img alt={props.alt} src={props.image} className={classes.image}/>
                                    </Grid>
                                ): null
                            }
                            
                            <Grid item xs={12} sm={12} xl={12} lg={12}>
                                {
                                    props.title ? (
                                        <Typography 
                                            variant="body1"
                                            color="primary"
                                            component="h1"
                                            display="inline"
                                        
                                        >
                                            {props.title}
                                            
                                        </Typography>
                                    ):null
                                }
                                {
                                    props.subtitle ? (
                                        <Typography 
                                            variant="body2"
                                            color="textSecondary"
                                            component="h2"
                                            gutterBottom={true}
                                        >
                                            {props.subtitle}
                                        </Typography>
                                    ):null
                                }
                                {
                                    props.testimonial ? (
                                        <Typography 
                                            variant="caption"
                                            color="textSecondary"
                                            component="p"
                                        >
                                            {props.testimonial}
                                        </Typography>
                                    ): null
                                }
                                
                            </Grid>
                            
                        </Grid>
                    </Paper>
                </Slide>
            ): (
                <Paper className={classes.root} elevation={props.shadow ? props.shadow : 1}>
                        <Grid container spacing={1}>
                        {
                                props.image ? (
                                    <Grid item xs={12} sm={12} xl={12} lg={12}>
                                        <img alt={props.alt} src={props.image} className={classes.image}/>
                                    </Grid>
                                ): null
                            }
                            <Grid item xs={12} sm={12} xl={12} lg={12}>
                            {
                                    props.title ? (
                                        <Typography 
                                            variant="body1"
                                            color="primary"
                                            component="h1"
                                            display="inline"
                                        
                                        >
                                            {props.title}
                                            
                                        </Typography>
                                    ):null
                                }
                                {
                                    props.subtitle ? (
                                        <Typography 
                                            variant="body2"
                                            color="textSecondary"
                                            component="h2"
                                            gutterBottom={true}
                                        >
                                            {props.subtitle}
                                        </Typography>
                                    ):null
                                }
                                {
                                    props.testimonial ? (
                                        <Typography 
                                            variant="caption"
                                            color="textSecondary"
                                            component="p"
                                        >
                                            {props.testimonial}
                                        </Typography>
                                    ): null
                                }
                            </Grid>
                            
                        </Grid>
                    </Paper>
            )
        
        
    )
}

